import logo from './logo.svg';
import './App.css';
// import React from "react";
import React, { useState, useEffect, useRef } from "react";
import ScrollToTop from "./components/ScrollToTop";
import Menu from './components/Menu';
import './css/style.css';
import bgPattern from "./assets/bg_pattern_50.png";
//import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import { BrowserRouter, Router, Routes, Route } from "react-router-dom";

import HomePage from "./pages/HomePage";
import IranPage from "./pages/IranPage";
import IsraelPage from "./pages/IsraelPage";
import UAEPage from "./pages/UAEPage";
import TurkeyPage from "./pages/TurkeyPage";

function App() {
  const [isHidden, setIsHidden] = useState(true);
  const [showHamburger, setShowHamburger] = useState(true); 

  const parallaxRef = useRef(null);

  const parallaxStyle = {
    backgroundImage: `url(${bgPattern})`,
    backgroundRepeat: "repeat",
    backgroundSize: "100px 100px",
    height: "100%",
  };

  useEffect(() => {
    const handleScroll = () => {
      const scrollPosition = window.pageYOffset;
      if (parallaxRef.current) {
        parallaxRef.current.style.backgroundPositionY = `${scrollPosition * 0.5}px`;
      }
    };
  
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  return (
    <>
  <div
      ref={parallaxRef}
      style={parallaxStyle}
    >
    
    <Router>
    <ScrollToTop />
    <div className="App">
      {showHamburger && (
        <i
          id="hamburger-btn"
          className="material-icons"
          onClick={() => {
            setIsHidden(false);
            setShowHamburger(false);
          }}
        >
          menu
        </i>
      )}
      <Menu isHidden={isHidden} setIsHidden={setIsHidden} setShowHamburger={setShowHamburger} />
  
      <Routes>
        <Route path="/" element={<HomePage />} /> 
        <Route path="/iran" element={<IranPage />} />
        <Route path="/israel" element={<IsraelPage />} />
        <Route path="/uae" element={<UAEPage />} />
        <Route path="/turkey" element={<TurkeyPage />} />
      </Routes>
 



      {/* <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Edit <code>src/App.js</code> and save to reload.
        </p>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
      </header> */}
      
    </div>
    </Router>
    </div>
    </>
  );
}

export default App;
