const PageContent = ({ description, terrainImg, spots, videoIframe, betweenChild }) => {
  return (
    <div id="page-content-container">
      <div id="page-content-wrapper">
        <div id="description">
          {description}
        </div>
        <div id="terrain">
          {typeof terrainImg === "string" ? <img src={terrainImg} alt="Terrain" /> : terrainImg} {/* 支援字串或者 JSX */}
        </div>
        {/* Child Section */}
        {betweenChild && <div id="between-child">{betweenChild}</div>}
        {spots.map((spot, index) => (
          <div className="spot" key={index}>
            <div>{typeof spot.image === "string" ? <img src={spot.image} alt={`Spot ${index}`} /> : spot.image}</div> {/* 渲染 JSX 或字串 */}
            <div>{spot.text}</div>
          </div>
        ))}
      </div>
      <div id="page-content-video">
        {typeof videoIframe === "string" ? (
          <div dangerouslySetInnerHTML={{ __html: videoIframe }} />
        ) : (
          videoIframe
        )}
      </div>
    </div>
  );
};

export default PageContent;
