import React, { useEffect } from "react";
import { motion } from "framer-motion"
import anime from "animejs";
import Footer from "../components/Footer";
import PageContent from "../components/PageContent";
import '../css/style.css';
import '../css/Pages.css';

const TurkeyPage = () => {
  const headerSVG = "./images/turkey_page.svg" 
  const descriptionText = "土耳其橫跨歐亞大陸，因其地理位置而成為多個文明的交匯之地。其歷史可以追溯到古代安那托利亞時期，之後經歷了拜占庭帝國及奧斯曼帝國的輝煌時代。奧斯曼帝國持續了六百多年，並在文化、藝術及建築方面留下了深遠影響，包括著名的藍色清真寺和托普卡帕宮。現代土耳其於1923年成立，由穆斯塔法·凱末爾·阿塔圖爾克領導，推動了世俗化與現代化改革。土耳其文化融合了東方與西方的元素，擁有美食如土耳其茶和烤肉，以及傳統音樂和舞蹈形式如旋轉舞者（Sufi舞）。這個國家不僅擁有悠久的歷史，更以其多元文化而聞名。（By AI）";
  const terrainImage = "./images/turkey_terrain.svg";
  const spotData = [
    {
      image: "./images/cw-image-resizer.cwg.jpg",
      text: "聖索菲亞大教堂（Hagia Sophia） 具有深厚歷史的地標，曾是拜占庭教堂和奧斯曼清真寺，現在是一座博物館，融合了宗教與建築的瑰麗之美。（By AI）"
    },
    {
      image: "./images/balloon01-1024x542.jpeg",
      text: "卡帕多奇亞（Cappadocia） 因其奇特的「童話煙囪」岩石地貌和壯觀的熱氣球體驗而聞名，仿如走進神話世界。（By AI）"
    },
    {
      image: "./images/troy-scaled.jpeg",
      text: "特洛伊古城（Troy） 傳說中木馬計的發源地，擁有豐富的考古遺址，展示了古希臘與特洛伊的歷史傳奇。（By AI）"
    },
  ];
  const iframeHTML = (
    <iframe src="https://www.youtube.com/embed/89l36qVc3lc?si=wZ9hzMG_2EQWAg1F" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" referrerpolicy="strict-origin-when-cross-origin" allowfullscreen></iframe>
  );
  const betweenChildContent = null;

  useEffect(() => {
    const objectElement = document.querySelector("#svgObject");
    objectElement.addEventListener("load", () => {
      const svgDocument = objectElement.contentDocument;
      const logoElsElements = Array.from(svgDocument.querySelectorAll(".logoEls"));

      console.log("LogoEls Elements:", logoElsElements);
      if (logoElsElements.length > 0) {
        anime({
          targets: logoElsElements,
          keyframes: [
            { translateY: -10, easing: 'easeInOutQuad' },
            { translateY: 0, easing: 'easeOutBounce' },
          ],
          duration: 900,
          loop: true,
          delay: (el, i) => i * 60,
        });
      } else {
        console.error("no .logoEls");
      }
    });
  }, []);

  return (
    <>
      {/* <div>這是以色列頁面</div> */}
      <motion.div
        id="svg-wrapper"
        initial={{ opacity: 0, scale: 0.8 }}
        whileInView={{ opacity: 1, scale: 1 }}
        transition={{ duration: 1 }}
        viewport={{ once: true }}
      >
        <object
          id="svgObject"
          type="image/svg+xml"
          data={headerSVG}
          style={{ width: "100%", height: "auto" }}
        >
          Your browser does not support embedded SVGs.
        </object>
      </motion.div>

      <motion.div
        id="flag"
        initial={{ y: -50, opacity: 0 }}
        whileInView={{ y: 0, opacity: 1 }}
        transition={{ duration: 1 }}
        viewport={{ once: true, amount: 0.4 }}
      >
        🇹🇷
      </motion.div>

      {/* Individual motions for PageContent elements */}
      <motion.div
        initial={{ opacity: 0, y: 50 }}
        whileInView={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.8 }}
        viewport={{ once: true, amount: 0.2 }}
        
      >
        <PageContent
          description={
            <motion.div
              initial={{ y: 50, opacity: 0 }}
              whileInView={{ y: 0, opacity: 1 }}
              transition={{ duration: 1 }}
            >
              {descriptionText}
            </motion.div>
          }
          terrainImg={
            <motion.div
              initial={{ opacity: 0, scale: 0.9 }}
              whileInView={{ opacity: 1, scale: 1 }}
              transition={{ duration: 1 }}
            >
              <img src={terrainImage} alt="Terrain" />
            </motion.div>
          }
          spots={spotData.map((spot, index) => ({
            image: (
              <motion.img
                key={`image-${index}`}
                src={spot.image}
                alt={`Spot ${index}`}
                initial={{ y: 50, opacity: 0 }}
                whileInView={{ y: 0, opacity: 1 }}
                transition={{ duration: 1, delay: index * 0.2 }}
              />
            ),
            text: (
              <motion.div
                key={`text-${index}`}
                initial={{ y: 50, opacity: 0 }}
                whileInView={{ y: 0, opacity: 1 }}
                transition={{ duration: 1, delay: index * 0.2 }}
              >
                {spot.text}
              </motion.div>
            )
          }))}
          videoIframe={
            <motion.div
              initial={{ opacity: 0 }}
              whileInView={{ opacity: 1 }}
              transition={{ duration: 1 }}
            >
              {iframeHTML}
            </motion.div>
          }
        />
      </motion.div>

      <Footer />
    </>
  )
};

export default TurkeyPage;