import React from "react";
import { Link } from "react-router-dom";
import "../css/ArticleInfo.css";

function ArticleInfo({ title, date, description, content, image, link }) {
  return (
    <div id="article-info-container">
      <div>
        {typeof image === "string" ? (
          <img src={image} alt="Article" />
        ) : (
          image
        )}
      </div>

      <div className="info">
        <table className="info-table">
          <tbody>
            <tr>
              <td className="first bold">
                {typeof title === "string" ? (
                  <Link to={link}>{title}</Link>
                ) : (
                  title /* 支援 JSX */
                )}
              </td>
              <td className="date bold">
                {typeof date === "string" ? date : date}
              </td>
            </tr>
            <tr>
              <td className="text-flow" colSpan="2">
                {typeof description === "string"
                  ? description
                  : description}
              </td>
            </tr>
          </tbody>
        </table>
        <div>
          {typeof content === "string" ? (
            <Link to={link} className="content-link">
              {content}
            </Link>
          ) : (
            content
          )}
        </div>
      </div>
    </div>
  );
}

export default ArticleInfo;
